<template>
  <el-form :model="form" label-width="90px" size="default">
    <el-form-item label="解析类型">
      <el-col :span="3">
        <el-select v-model="form.type" placeholder="请选则类型" @change="onChange">
          <el-option v-for="(item, index)  in options" :label="item.label" :value="item.value" :key="index">
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="1" />
      <el-col :span="2">
        <el-button type="default" :icon="Delete" @click="onClear">清空</el-button>
      </el-col>

      <el-col :span="2">
        <el-button type="success" :icon="Download" v-show="tableData.length >= 1" @click="onExport">下载</el-button>
      </el-col>
    </el-form-item>
    <el-form-item label="内容">
      <el-input v-model="form.copyData" type="textarea" :autosize="{ minRows: 5, maxRows: 12 }" @change="onChange"
        @input="onChange" />
    </el-form-item>
    <el-form-item label="表">
      <el-table :data="tableData" border stripe style="width: 100%" height="360"
        :header-row-style="{ 'font-size': '10px' }" :row-style="{ 'font-size': '12px' }" size="small" empty-text="没有数据">
        <el-table-column fixed prop="cpxh" label="产品序号" width="100" />
        <el-table-column prop="cpdm_kh" label="产品代码（客户）" width="100" />
        <el-table-column prop="cpdm" label="产品代码" width="100" />
        <el-table-column prop="cpms" label="产品描述" width="320" />
        <el-table-column prop="gg" label="规格" width="80" />
        <el-table-column prop="zwfy" label="中文翻译" width="80" />
        <el-table-column prop="sl" label="数量" width="80" />
        <el-table-column prop="dw" label="单位" width="80" />
        <el-table-column prop="cbj" label="成本价" width="80" />
        <el-table-column prop="sj" label="售价" width="80" />
        <el-table-column prop="gysdm" label="供应商代码" width="90" />
        <el-table-column prop="cbm" label="船部门" width="80" />
        <el-table-column prop="bz" label="备注" width="80" />
        <el-table-column prop="chzs" label="采购注释" width="100" />
        <el-table-column prop="pp" label="品牌" width="80" />
      </el-table>

    </el-form-item>

  </el-form>

</template>

<script>
import {
  Delete,
  Download,
} from '@element-plus/icons-vue'
import { onMounted, reactive, ref, toRefs, computed, watchEffect, nextTick, onBeforeMount } from 'vue';
import { ElMessage } from 'element-plus'
const ExcelJS = require('exceljs');
import { saveAs } from 'file-saver';

export default {
  name: 'form1',
  //components:{Delete,Download},
  setup() {
    const options = [{
      value: 0,
      label: '类型1'
    }, {
      value: 1,
      label: '类型2'
    }]
    const form = reactive({
      copyData: '',
      type: 0

    })
    let tableData = reactive([])

    const initdata = () => {
      let arr = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
      let obj = {
        cpxh: '',
        cpdm_kh: '',
        cpdm: '',
        cpms: '',
        gg: '',
        zwfy: '',
        sl: '',
        dw: '',
        cbj: '',
        sj: '',
        gysdm: '',
        cbm: '',
        bz: '',
        chzs: '',
        pp: ''
      }
      return { arr, obj }
    }

    const type = (txt, tableDataArray, typeName) => {
      tableData.length = 0
      try {
        let txtArr = txt.split('\n')
        let linenum = txtArr.length
        console.log(linenum);
        for (let i = 0; i < linenum; i++) {
          let row = txtArr[i]
          if (row.trim().length == 0) {
            //空行
            continue
          }
          let { arr: rowArr_input, obj: rowObj } = initdata()
          funs[typeName](row, rowArr_input, rowObj)
          //console.log(rowArr_input);
          tableDataArray.push(rowArr_input)
          tableData.push(rowObj)
        }
        return tableDataArray
      } catch (e) {
        console.log(e);
        return null
      }
    }

    const type1 = (row, rowArr_input, rowObj) => {
      let rowArr = row.split(/\s{4}/g)
      rowArr_input[6] = rowArr[0]//数量 
      rowObj.sl = rowArr[0]
      rowArr_input[7] = rowArr[1]//单位
      rowObj.dw = rowArr[1]
      rowArr_input[3] = rowArr[2]//产品描述
      rowObj.cpms = rowArr[2]
      for (let j = 3; j < rowArr.length; j++) {
        rowArr_input[3] += '    ' + rowArr[j]
        rowObj.cpms += '    ' + rowArr[j]
      }
    }
    const type2 = (row, rowArr_input, rowObj) => {
      let rowArr = row.split(/[0-9][0-9][0-9]:\s/g)
      rowArr = rowArr[1].split(/\s/g)
      rowArr_input[6] = rowArr[0]//数量
      rowObj.sl = rowArr[0]
      rowArr_input[7] = rowArr[1]//单位
      rowObj.dw = rowArr[1]
      rowArr_input[3] = rowArr[2]//产品描述
      rowObj.cpms = rowArr[2]
      for (let j = 3; j < rowArr.length; j++) {
        if (j == rowArr.length - 1) {
          if (/^\d{6,}$/g.test(rowArr[j])) {
            rowArr_input[1] = rowArr[j]
            rowObj.cpdm_kh = rowArr[j]
            continue
          }
          // 
        }
        rowArr_input[3] += ' ' + rowArr[j]
        rowObj.cpms += '    ' + rowArr[j]
      }
    }
    const funs = [type1, type2]
    //预处理
    const mydeal = (tbDataArray) => {
      console.log(form.type);
      tbDataArray = [
        ['产品序号', '产品代码（客户）', '产品代码', '产品描述', '规格', '中文翻译', '数量', '单位', '成本价', '售价', '供应商代码',
          '船部门', '备注', '采购注释', '品牌']
      ]
      //funs[form.type](form.copyData, tbDataArray)
      type(form.copyData, tbDataArray, form.type)
      //tableDataArray.push(col)
      //console.log(tbDataArray);
      if (tbDataArray == null) {
        ElMessage({
          message: '识别失败',
          type: 'warning',
        })
        return false
      }
      return tbDataArray
    }
    //清空
    const onClear = () => {
      form.copyData = null
      onChange()
    }
    //导出
    const onExport = () => {
      let tableDataArray = []
      tableDataArray = mydeal(tableDataArray)
      if (tableDataArray == false) {
        return false
      }
      console.log(tableDataArray);
      const excelExport = async (n) => {
        const workbook = new ExcelJS.Workbook();

        const worksheet = workbook.addWorksheet('Sheet1');
        worksheet.columns = [
          { width: 10 },
          { width: 20 },
          { width: 10 },
          { width: 50 },
          { width: 20 },
          { width: 20 },
          { width: 10 },
          { width: 10 },
          { width: 10 },
          { width: 10 },
          { width: 20 },
          { width: 20 },
          { width: 20 },
          { width: 20 },
          { width: 20 },
        ];
        console.log(worksheet.columns);
        const data = tableDataArray
        worksheet.addRows(data);
        console.log(worksheet.columns);
        worksheet.getRow(1).font = { bold: true };
        workbook.xlsx.writeBuffer().then(buffer => {
          saveAs(new Blob([buffer], { type: 'application/octet-stream' }), '表格导出.xlsx');
        });
      }
      excelExport();

    }
    //内容变更
    const onChange = () => {
      let tableDataArray
      tableDataArray = mydeal(tableDataArray)
      if (tableDataArray == false) {
        return false
      }
    }

    return {
      options,
      form,
      onExport,
      onChange,
      onClear,
      tableData, Delete, Download,
    }
  }
};
</script>
